// --- SUB-COMPONENTS ---

function NavButton({ view, current, set, icon, label }: { view: View, current: View, set: any, icon: any, label: string }) {
  const isActive = current === view;
  return (
    <button 
      onClick={() => set(view)}
      className={`w-full flex items-center gap-3 px-4 py-3 rounded-xl transition-all duration-200 group ${
        isActive 
          ? 'bg-zinc-800 text-white shadow-inner' 
          : 'text-zinc-400 hover:text-white hover:bg-zinc-800/50'
      }`}
    >
      <span className={isActive ? 'text-indigo-400' : 'text-zinc-500 group-hover:text-zinc-300'}>{icon}</span>
      <span className="font-medium text-sm">{label}</span>
      {isActive && <ChevronRight size={14} className="ml-auto text-zinc-600" />}
    </button>
  );
}

function SocialIcon({ icon }: { icon: any }) {
  return (
    <a href="#" className="p-2 text-zinc-500 hover:text-white hover:bg-zinc-800 rounded-lg transition-colors">
      {icon}
    </a>
  );
}

// --- VIEW: CHATBOT (The Core Feature) ---
function ChatView() {
  const [messages, setMessages] = useState<Message[]>([
    { role: 'assistant', content: "Hello. I am the digital twin of Alex. I have been trained on his resume, code repositories, and technical blogs. Ask me anything about his skills, experience, or design philosophy." }
  ]);
  const [input, setInput] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const bottomRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    bottomRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const handleSend = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!input.trim()) return;

    const userMsg: Message = { role: 'user', content: input };
    setMessages(prev => [...prev, userMsg]);
    setInput('');
    setIsTyping(true);

    // --- MOCK API CALL (Replace with your Vector DB/LLM Endpoint) ---
    setTimeout(() => {
      const responses = [
        "Based on Alex's experience at TechNova, he specializes in optimizing LLM inference latency. He reduced costs by 30% using quantization.",
        "Alex is proficient in Python and Go. He prefers Next.js for frontend work, as seen in the GiftGenie project.",
        "Yes, he is available for freelance work. You can contact him via the Contact tab.",
      ];
      const randomResponse = responses[Math.floor(Math.random() * responses.length)];
      
      setMessages(prev => [...prev, { role: 'assistant', content: randomResponse }]);
      setIsTyping(false);
    }, 1500);
  };

  return (
    <div className="max-w-3xl mx-auto h-full flex flex-col">
      <div className="flex-1 space-y-6 pb-6">
        {messages.map((m, i) => (
          <div key={i} className={`flex gap-4 ${m.role === 'user' ? 'flex-row-reverse' : ''}`}>
            <div className={`w-8 h-8 rounded-full flex items-center justify-center shrink-0 ${
              m.role === 'assistant' ? 'bg-indigo-600' : 'bg-zinc-700'
            }`}>
              {m.role === 'assistant' ? <Terminal size={16} className="text-white"/> : <User size={16} className="text-zinc-300"/>}
            </div>
            <div className={`p-4 rounded-2xl max-w-[80%] text-sm leading-relaxed shadow-sm ${
              m.role === 'assistant' 
                ? 'bg-zinc-900 border border-zinc-800 text-zinc-300' 
                : 'bg-zinc-200 text-zinc-900 font-medium'
            }`}>
              {m.content}
            </div>
          </div>
        ))}
        {isTyping && (
           <div className="flex gap-4">
             <div className="w-8 h-8 bg-indigo-600 rounded-full flex items-center justify-center shrink-0">
               <Terminal size={16} className="text-white"/>
             </div>
             <div className="flex items-center gap-1 h-10 px-4 bg-zinc-900 border border-zinc-800 rounded-2xl">
               <div className="w-2 h-2 bg-zinc-500 rounded-full animate-bounce" style={{ animationDelay: '0ms' }}></div>
               <div className="w-2 h-2 bg-zinc-500 rounded-full animate-bounce" style={{ animationDelay: '150ms' }}></div>
               <div className="w-2 h-2 bg-zinc-500 rounded-full animate-bounce" style={{ animationDelay: '300ms' }}></div>
             </div>
           </div>
        )}
        <div ref={bottomRef} />
      </div>

      <div className="sticky bottom-0 bg-zinc-950/80 backdrop-blur-md pb-4 pt-2">
        <form onSubmit={handleSend} className="relative">
          <input 
            type="text" 
            value={input}
            onChange={(e) => setInput(e.target.value)}
            placeholder="Ask about my projects, stack, or experience..."
            className="w-full bg-zinc-900 border border-zinc-800 text-white rounded-xl pl-4 pr-12 py-4 focus:outline-none focus:ring-2 focus:ring-indigo-500/50 transition-all shadow-xl"
          />
          <button 
            type="submit"
            className="absolute right-2 top-2 p-2 bg-indigo-600 hover:bg-indigo-500 text-white rounded-lg transition-colors"
          >
            <Send size={18} />
          </button>
        </form>
        <div className="flex gap-2 mt-3 overflow-x-auto pb-2 no-scrollbar">
           {["What is your tech stack?", "Tell me about TechNova", "Do you know Docker?"].map((q, i) => (
             <button key={i} onClick={() => setInput(q)} className="whitespace-nowrap px-3 py-1.5 bg-zinc-900 border border-zinc-800 hover:border-indigo-500/50 rounded-full text-xs text-zinc-400 hover:text-white transition-colors">
               {q}
             </button>
           ))}
        </div>
      </div>
    </div>
  );
}

